
html,
body,
#app,
.view {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.view {
  width: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
p {
  line-height: 27px;
}
pre {
  border-color: #d3daea;
  display: block;
  word-break: break-all;
  word-wrap: break-word;
  color: #333;
  background-color: #f5f7fa;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 30px;
  padding: 0 10px;
}
pre code {
  color: #f66;
  padding: 0;
  font-size: inherit;
  color: inherit;
  white-space: pre-wrap;
  background-color: transparent;
  border-radius: 0;
  line-height: 30px;
}
blockquote {
  color: #666;
  margin: 0;
  padding-left: 3em;
  border-left: 0.5em #eee solid;
}
blockquote p {
  margin: 0px;
  line-height: 26px;
}
ul li {
  line-height: 30px;
}
hr {
  box-sizing: content-box;
  height: 0;
}
table,
table>thead>tr>th,
table>thead>tr>td,
table>tbody>tr>th,
table>tbody>tr>td,
table>tfoot>tr>th,
table>tfoot>tr>td {
  border: 1px solid #ddd;
  padding: 10px;
}
table {
  border-collapse: collapse;
}
a {
  color: #a0aabf;
  text-decoration: underline;
}
code {
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px;
}


